import {defaultTurnEnded , defaultTurnStarted} from "./functions.js"
import {defaultBattleEnded , defaultBattleStarted} from "./functions.js"


const pubsub = ( function(){

let turn = 0
let battle = null

const events = { 
  "entityDied" : [] ,
}

function on(event , obj) {
  events[event] = events[event] || []
  events[event].push(obj)
}

function off(event , obj) {
if(!(event in events)){alert("trying to off event that doesn't exist"); console.log(event , obj); return}

    for(let i= 0 ; i < events[event].length  ;i++){
      if(events[event][i] === obj){
        events[event].splice(i,1)
        break;
      }
    }
}

function emit(event , data) {
  events[event] = events[event] || []
  Array.from(events[event]).forEach(evenObject => {
    evenObject?.effect(data)
  });
}

    function battleStarted(type_of_battle){
      battle = type_of_battle

      defaultBattleStarted(battle)

      Array.from(events["battleStarted"]).forEach(evenObject => {
        evenObject?.effect(battle)
      });
    }

    function battleEnded(won = true){
    // if (!battle) return // protect against multple enemies triggering this at the same time

      Array.from(events["battleEnded"]).forEach(evenObject => {
        evenObject?.effect(battle)
      });

      defaultBattleEnded(battle)

      battle = null
    }

    function turnStarted() {
    Array.from(events["turnStarted"] || []).forEach(evenObject => {
        evenObject?.effect(turn)
      });
      defaultTurnStarted()

      turn++
      console.log(`%c Turn%c:${turn} started` , "color:red;" , "color:black;");
    }
    function turnEnded() {
      Array.from(events["turnEnded"]).forEach(evenObject => {
        evenObject?.effect(turn)
      });

      defaultTurnEnded()
    }


function resetTurns(){ turn = 0}

return{ events ,on,off,emit,
  battleStarted , battleEnded,turnStarted,turnEnded, resetTurns  }
  })()

export default pubsub ;