import * as dom from "../dom.js"

export function render_basic_attack_icon(move) {
  const action_icon = move.entity.actionIcon
  const icon = dom.attack_icon.cloneNode(true)
  icon.classList.remove("hidden")

  action_icon.innerText = ""
  action_icon.append(  icon, move.damage)
}

