import * as buffs from "./buffs/buffClasses.js"
import * as debuffs from "./buffs/debuffClasses.js"
const effects = {...buffs , ...debuffs}
export default effects 


export function forceRemoveEffects(entity) {
  for(const effect in entity.buffs){
    entity["buffs"][effect]._forceRemove()
  }
  for(const effect in entity.debuffs){
    entity["debuffs"][effect]._forceRemove()
  }
}